export default {
	data(){
		return {
			providerList:[],				
			sourceLink: 'http://yunzhujiao.cn/bind_pub/index.html',		
			type:0,
		}
	},
	onLoad() {
		this.version = plus.runtime.version;
		uni.getProvider({
			service: 'share',
			success: (e) => {
				let data = [];
				for (let i = 0; i < e.provider.length; i++) {
					switch (e.provider[i]) {
						case 'weixin':
							data.push({
								name: '分享到微信好友',
								id: 'weixin'
							})
							data.push({
								name: '分享到微信朋友圈',
								id: 'weixin',
								type: 'WXSenceTimeline'
							})
							break;
						case 'qq':
							data.push({
								name: '分享到QQ',
								id: 'qq'
							})
							break;
						default:
							break;
					}
				}
				this.providerList = data;
			},
			fail: (e) => {
				console.log('获取登录通道失败'+ JSON.stringify(e));
			}
		});
	},
	methods:{
		
		//复制分享链接
