						content: '内容已复制到粘贴板，可前往其他应用粘贴查看。', 
						showCancel:false,
						success: function(res) {
							if (res.confirm) {											 
								//console.log('用户点击确定');
							} else if (res.cancel) {
								//console.log('用户点击取消');
							}
						}
					});
				}
			});
		},
		//保存图片到相册
		save(){
			uni.showActionSheet({
				itemList:['保存图片到相册'],
				success: () => {
					plus.gallery.save('http://pds.jyt123.com/wxtest/logo.png', function() {
						uni.showToast({
							title:'保存成功',
							icon:'none'
						})
					}, function() {
						uni.showToast({
							title:'保存失败，请重试！',
							icon:'none'
						})
					});
				}
			})
